<template>
    <div>
        <section id="hero2">
            <div class="container">
                <div class="row">
                    <div class="col-lg-10 col-md-12 col-sm-12">
                        <div class="hero-wrapper2">
                            <div class="hero-details">
                                <div class="hero-title">
                                    <h1>{{ $store.state.appName }} Services</h1>
                                    <div class="hero-text">
                                        <!--                                        <p>All about algebra</p>-->
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>

        <div id="category" class="container">
            <spinner :data="spinnerData"></spinner>
            <div class="row">
                <div data-toggle="modal" data-target="#unread-messages" @click="getProducts(industry.industry_id)" class="col-lg-3 col-md-6 col-sm-12" v-for="(industry, index) in industriesArr " :key="index">
                    <div class="category">
                        <div class="category__image">
                            <img src="img/category/cat1.png" alt="">
                            <span class="category__icon" :style=" 'font-size: 30px;color:' + industry.icon_color " ><i class="fas fa-atom"></i></span>
                        </div>
                        <div class="category__info">
                            <h6><a data-toggle="modal" data-target="#unread-messages" href="javascript:;">{{ industry.industry_name }}</a></h6>
                        </div>
                    </div>

                </div>

            </div>
        </div>
        <ProductsModal></ProductsModal>
    </div>
</template>

<script>
    import ProductsModal from "../../components/Modals/ProductsModal";

    export default {
        title: 'Services',
        name: "index",
        components: {ProductsModal},
        data() {
            return {
                industriesArr: {},
                spinnerData: {},
            }
        },
        mounted() {
            this.getIndustries();
        },
        methods: {
            showLoading(options = {}) {
                let loader = this.spinnerData = {
                    spinner: 'Google',
                    isLoading: options.show !== undefined ? options.show : true ,
                    color: '#00bfff',
                    size: '60px',
                    options: 'center absolute',
                };
                return loader;
            },
            getRandomColor() {
                var letters = '0123456789ABCDEF';
                var color = '#';
                for (var i = 0; i < 6; i++) {
                    color += letters[Math.floor(Math.random() * 16)];
                }
                return color;
            },
            getProducts(id) {
                window.eventBus.$emit("industryId", id);
            },
            getIndustries() {
                this.showLoading();
                this.axios
                    .get("industries/all")
                    .then(response => {
                        this.showLoading({show:false});
                        // console.log(response.data.data.industries);
                        let industries = response.data.data.industries;
                        let industryData = [];

                        for (var key in industries) {
                            var value = industries[key];
                            var formFields = {
                                industry_description: value.industry_description,
                                industry_id: value.industry_id,
                                industry_name: value.industry_name,
                                icon_color: this.getRandomColor(),
                            };
                            industryData.push(formFields);
                        };
                        this.industriesArr = industryData;
                    })
                    .catch(error => {
                        this.showLoading({show:false});
                        // console.log(error.response);
                        return error;
                    });
            },
        },
    }
</script>

<style scoped>
    .category {
        text-align: center;
        padding-bottom: 40px;
        border: 1px solid var(--border-one);
        border-radius: 20px;
        transition: all 0.5s;
        min-height: 0 !important;
        cursor: pointer;
    }
</style>
